import React, { Component } from 'react';
import { connect } from "react-redux";
import ReactHtmlParser from 'react-html-parser';
import HtmlParser from 'react-html-parser';
import { Nav, Tab, Table, Badge, Image, Button } from 'react-bootstrap';
import { GET_NETWORK_ICON, MEDIA_REPLACE_URL, GET_NETWORK_NAME, SENTIMENTS_CALC, getPlainText, addLineBreaks } from '../../../../../../utils';
// import { postPostUpdateAction, loadAssetsDataWithDispatch, tagsFilter } from '../../../../Services/Actions/assetsActions'
import { baseUrl, FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import Author from '../../../CommonComponents/Author/Author';
import TagProducts from './TagProducts';
import Rating from '../../../CommonComponents/Rating';
import { MediaBaseUrl } from '../../../../../../actions/api';

class ContentModalAside extends Component {
    constructor(props) {
        super(props);
        this.state = {
            //tagString:['#macdonald', 'Restaurant', 'Burger', '#macdonaldUSA', 'California', '#starbucks', 'Restaurant'],
            descriptionEditable: false,
            addTagButton: true,
            //tagInputValue : ''
            tagString: [],
            newTag: "",
            listTags: [],
            selectedOption: null,
            content: "",
            changesStatus: false
        };

        this.editorEnable = React.createRef();
        this.editorEnableButton = React.createRef();
    }

    onClickToRemoveTag = (index) => event => {
        let { tagString } = this.state;
        tagString.splice(index, 1)
        this.setState({ tagString, changesStatus: true })

    }

    componentWillMount() {
        const { tagList } = this.props
        if (tagList && tagList.length > 0) {
            var listTags = []
            tagList.map((item) => {
                listTags.push({ value: item, label: item })
            })
            this.setState({ listTags })


        }

    }

    addTagButtonValue = () => {
        this.setState({
            addTagButton: false
        })
    }

    // tagInputValueFun = (value) => {
    //     this.setState({
    //         tagInputValue : value
    //     })
    // }

    descriptionToggle() {
        this.setState({
            descriptionEditable: true
        })
    }

    componentDidMount() {
        document.addEventListener('click', this.handleOutsideClick);
        this.setInitialState();
    }

    componentDidUpdate(prevProps) {
        if (prevProps.modalData !== this.props.modalData) {
            this.setInitialState();
        }
    }
    setInitialState = () => {
        const { modalData } = this.props
        if (modalData && Object.keys(modalData).length > 0) {
            if (modalData.tags && String(modalData.tags).length > 0) this.setState({ tagString: String(modalData.tags).split(","), content: modalData.postcontent })
            else this.setState({ tagString: [], content: modalData.postcontent })
        }
        this.setState({
            textAreaHeight: (this.state.content.length / 35).toFixed() * 20
        })
    }

    handleOutsideClick = (event) => {
        if (this.editorEnable && this.editorEnable.current && !this.editorEnable.current.contains(event.target) && (this.editorEnableButton && !this.editorEnableButton.current.contains(event.target)) && this.editorEnableButton && !this.editorEnableButton.current.contains(event.target) !== null) {
            this.setState({
                descriptionEditable: false
            })
        } else {
            if (this.editorEnable && this.editorEnable.current && (!this.editorEnable.current.contains(event.target) && this.editorEnable.current.contains(event.target) !== null)) {
                this.setState({
                    descriptionEditable: true
                })
            }
        }
    }

    onClickNewAddTag = event => {
        const { newTag, tagString } = this.state;
        const { modalData } = this.props

        if (event.key === 'Enter' || event.keyCode === 13) {
            if (newTag && String(newTag).length >= 3) {
                tagString.push(newTag)
                this.setState({ tagString, newTag: "", selectedOption: null, changesStatus: true, addTagButton: true })
            }
            else {

            }
        }
    }

    addNewTag = event => this.setState({ newTag: event.target.value, changesStatus: true })

    onClickToSave = event => {
        const { tagString } = this.state;
        const { modalData } = this.props;
        if (tagString && tagString.length > 0) {
            // this.props.postAddTag({
            //     "postId": String(modalData.id),
            //     "tags": tagString.join(",")
            // }, () => this.props.loadAssetsDataWithDispatch()) 
        }

    }

    setNewTags = (selectedOption) => {
        if (selectedOption && selectedOption.value) this.setState({ newTag: selectedOption.value, selectedOption })
        else this.setState({ newTag: "", selectedOption: null })


    };

    onChangeContent = event => {
        this.setState({ content: event.target.value, changesStatus: true })
    }
    onChangeTag = event => {
        this.setState({ newTag: event.target.value })
    }
    onSaveChanges = event => {
        const { modalData } = this.props;
        const { tagString, content } = this.state;
        const data = {
            "action": "4",
            "source": String(modalData.collection),
            "destination": String(modalData.collection),
            "postId": String(modalData.id),
            "postFile": modalData.postFile,
            "tags": tagString.join(","),
            "content": content
        }
        // this.props.postPostUpdateAction(data, () => {
        //     this.props.loadAssetsDataWithDispatch()
        //     this.setState({ descriptionEditable: false, addTagButton: true, changesStatus: false })
        //     //this.props.editMediaCallback(false)
        // })

    }
    onFilterWithTag = (value) => event => {
        const { modalCallback, tagsFilter } = this.props
        tagsFilter(value)
        setTimeout(() => modalCallback(false), 200)
    }

    contentHeight = (content) => {
        var contentLines = (content.length / 35)
        var contentLinesInt = Math.round(contentLines);
        return contentLines >= contentLinesInt ? (contentLinesInt + 1) * 20 + 20 : contentLinesInt * 20 + 20
    }

    render() {
        const { descriptionEditable, addTagButton, tagString, content, changesStatus, newTag } = this.state
        const { modalData, networkData, collections, isTrash, changeHotspotStatus, enableHotspot, activeCarousalPost, activeCarousalPostsList, carousalPostsList, updateCarousalPost, isShoppable } = this.props
        const modifiedNetworkId = modalData.networkId == 18 ? 2 : modalData.networkId
        const socialNetwork = networkData && networkData.length > 0 ? networkData.filter((network) => network.id == modifiedNetworkId) : [];
        const appContent = window.APP_CONTENT ?? {};

        return <Tab.Container id="modal_aside_" defaultActiveKey={isShoppable ? "tag_product" : "details"}>
            <Nav variant="underline" className='border-bottom'>
                {isShoppable ? <Nav.Item>
                    <Nav.Link className='ms-3 py-3' eventKey="tag_product">
                        <FontAwesomeIcon type={'solid'} icon={'bag-shopping'} iconClass={'me-1 fs-6'} />
                        Tag products
                    </Nav.Link>
                </Nav.Item> : null}
                <Nav.Item>
                    <Nav.Link className='ms-3 py-3' eventKey="details">
                        <FontAwesomeIcon type={'solid'} icon={'image'} iconClass={'me-1 fs-6'} />
                        Details
                    </Nav.Link>
                </Nav.Item>
            </Nav>
            <Tab.Content className='m_sideCont_scroll_'>
                <Tab.Pane eventKey="tag_product">
                    <TagProducts updateCarousalPost={updateCarousalPost} carousalPostsList={carousalPostsList} activeCarousalPostsList={activeCarousalPostsList} activeCarousalPost={activeCarousalPost} modalData={modalData} changeHotspotStatus={changeHotspotStatus} enableHotspot={enableHotspot} />
                </Tab.Pane>
                <Tab.Pane eventKey="details">
                    <div className={`position-relative`}>
                        <div className='px-4 mt-3'>
                            <Table className="table-borderless mb-0">
                                <tbody>
                                    <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_author ?? ""}</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className="d-flex align-items-center mr-2 cursor-pointer" onClick={modalData.link ? event => window.open(modalData.link, "_blank") : null}>
                                                <Author className={'symbol symbol-34 rounded-circle flex-shrink-0 me-2 overflow-hidden'}
                                                    imageSize={38}
                                                    symbolClass={'symbol-label fs-8'}
                                                    name={modalData.postname} profileUrl={MEDIA_REPLACE_URL(modalData.picture)} networkId={modalData.networkId ? modalData.networkId : 0} />
                                                <div>
                                                    <p className=" mb-0 text-break fw-bold">{modalData.postname}</p>
                                                    <small className="mb-0 text-muted text-break">@{modalData?.username?.replace(/\s+/g, '') }</small>
                                                </div>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_network ?? ""}</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className="d-flex align-items-center">
                                                <span className="me-2 d-flex">
                                                {modalData.networkId !=0 ? <img className="img-fluid" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(modalData.networkId)}.svg`)} height={18} width={18} alt={modalData.networkId ? modalData.networkId : 0} />:''}
                                                </span>
                                                <span className="">{modalData.networkId == 0 ? '': GET_NETWORK_NAME(modalData.networkId)}</span>
                                            </div>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_feed ?? ""}</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            {modalData.feedname && modalData.networkId == 32 ?
                                                <img className="img-fluid me-2" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(32)}.svg`)} height={18} width={18} alt="Chrome" />
                                                : null}
                                            <span className=" text-break">{modalData.feedname ? ReactHtmlParser(modalData.feedname) : 
                                            modalData.networkId == 0 ? <><img className="img-fluid" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(0)}.svg`)} height={18} width={18} alt={0} /> {appContent?.moderation?.manual_upload ?? ""} </>
                                            :'--'}</span>
                                        </td>
                                    </tr>
                                    {/* <tr>
                                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Created</span></td>
                                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className=" text-break">{moment(modalData.created).utcOffset(0).format("DD MMM YYYY hh:mmA")}</span></td>
                                            </tr>
                                            <tr>
                                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">Modified</span></td>
                                                <td className="py-xxl-2 py-1 px-0 w-75"> <span className=" text-break">{moment(modalData.modified).utcOffset(0).format("DD MMM YYYY hh:mmA")}</span></td>
                                            </tr> */}
                                    {
                                        modalData.ugcstatus ? <>
                                            {modalData.ugcstatus == 2 ? <tr>
                                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_rights ?? ""}</span></td>
                                                <td className="py-xxl-2 py-1 px-0 w-75">
                                                    <div className="d-flex align-items-center flex-wrap text-success">
                                                        <span className="fs-6 me-1">
                                                            <FontAwesomeIcon type={'solid'} icon={'user-check'} />
                                                        </span>
                                                        <span className="fw-medium">{appContent?.moderation?.rights_approved ?? ""}</span>
                                                    </div>
                                                </td>
                                            </tr> : modalData.ugcstatus == 1 ? <tr>
                                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_rights ?? ""}</span></td>
                                                <td className="py-xxl-2 py-1 px-0 w-75">
                                                    <div className="d-flex align-items-center flex-wrap text-warning">
                                                        <span className="fs-6 me-1">
                                                            <FontAwesomeIcon type={'solid'} icon={'user-clock'} />
                                                        </span>
                                                        <span className="fw-medium">{appContent?.moderation?.rights_pending ?? ""}</span>
                                                    </div>
                                                </td>
                                            </tr> : modalData.ugcstatus == 3 ? <tr>
                                                <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_rights ?? ""}</span></td>
                                                <td className="py-xxl-2 py-1 px-0 w-75">
                                                    <div className="d-flex align-items-center flex-wrap text-danger">
                                                        <span className="fs-6 me-1">
                                                            <FontAwesomeIcon type={'solid'} icon={'user-xmark'} />
                                                        </span>
                                                        <span className="fw-medium">{appContent?.moderation?.rights_rejected ?? ""}</span>
                                                    </div>
                                                </td>
                                            </tr> : null}
                                        </> : null
                                    }
                                    {parseInt(modalData.sentiment) > 0 ? <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.filter_sentiment ?? ""}</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75"> <span className=" text-break">
                                            <span className="d-flex fs-8  text-break">
                                                <div className='h-16px w-16px me-1 d-flex'>
                                                    <img className='img-fluid' src={baseUrl(`media/icons/emoji/emoji${SENTIMENTS_CALC(modalData.sentiment).item}.svg`)} height={16} width={16} alt='' />
                                                </div>
                                                {SENTIMENTS_CALC(modalData.sentiment).label}
                                            </span>

                                        </span></td>
                                    </tr> : null}
                                    {modalData.recommendation && (modalData.type == 2 || modalData.type == 4) ?
                                        <tr>
                                            <td className="py-xxl-2 py-1 px-0 min-w-100 w-25">
                                                <p className="text-muted mb-1 text-nowrap me-2">{appContent?.moderation?.filter_ai ?? ""}</p>
                                                {/* <Image className='invert' src={baseUrl('media/svg/trusense.svg')} height={36} width={110} fluid/> */}
                                                <span style={{ verticalAlign: "middle", fontStyle: "italic", fontSize: '11px', color: "#999", fontWeight: "400" }}>{appContent?.common?.powered_by ?? ""}
                                                    <img src={`${MediaBaseUrl}media/icons/svg-icons/true-sense.svg`} alt="TruSense" height={16} width={16} className='ms-1'/>
                                                    <span style={{ verticalAlign: "middle", fontSize: '12px', fontStyle: "normal", color: "var(--bs-gray-900)" }}> TruSense</span>
                                                </span>
                                            </td>
                                            <td className="py-xxl-2 py-1 px-0 w-75">
                                                <div className="d-flex align-items-start">
                                                    <div className={`h-12px w-12px rounded-circle d-flex flex-shrink-0 mt-1 ${modalData.recommendation.type == 1 ? 'bg-success' : modalData.recommendation.type == 2 ? 'bg-warning' : modalData.recommendation.type == 3 ? 'bg-danger' : null}`}></div>
                                                    <span className=" text-break ps-1">{modalData.recommendation.label} ({modalData.recommendation.score}%)</span>
                                                </div>
                                            </td>
                                        </tr>
                                        : null}
                                    {modalData.rating > 0 && modalData.type != 1 ? <tr>
                                        <td className="py-xxl-2 py-1 px-0 min-w-100 w-25"><span className="text-muted">{appContent?.moderation?.field_rating ?? ""}</span></td>
                                        <td className="py-xxl-2 py-1 px-0 w-75">
                                            <div className='d-flex justify-content-start'>
                                                <Rating className={'fs-6'} iconClass="me-1" rating={modalData.rating}/>
                                            </div>
                                        </td>
                                    </tr>: null}
                                </tbody>
                            </Table>
                        </div>
                        {tagString && tagString.length > 0 ? <>
                            <h3 className="border-bottom p-4 fs-8">
                                <span className="fw-medium">{appContent?.moderation?.btn_tags ?? ""}</span>
                            </h3>
                            <div className="tags-group d-flex align-items-center flex-wrap mb-2">
                                <div className='px-4 mt-3'>
                                    {
                                        tagString.map((Item, index) => {
                                            return Item && String(Item).length > 0 ? <Badge key={index} bg="" className={`${index == 99999 ? 'border-gradient' : 'border'}`}>
                                                <span className=''>{Item}</span>
                                            </Badge> : null

                                        })
                                    }
                                </div>
                            </div>
                        </>
                            : null}
                        <h3 className="border-bottom p-4 fs-8">
                            <span className="fw-medium">{appContent?.common?.description ?? ""}</span>
                        </h3>
                        <div className="px-4 mb-3">
                            <div className="d-flex flex-grow-1 pe-2">
                                <p style={{ whiteSpace: 'pre-line', wordBreak: 'break-word' }}>{HtmlParser(addLineBreaks(content || ""))}</p>
                            </div>
                        </div>
                    </div>
                </Tab.Pane>
            </Tab.Content>
        </Tab.Container>
    }
}


const mapStateToProps = (state) => {
    return {
        networkData: state?.networks?.networkData,
        tagList: state?.tags?.tagList,
        isShoppable: state.authorized?.userData?.user_data?.UserRule?.shoppableStatus
    }
}

const mapDispatchToProps = dispatch => {
    return {
        // postAddTag: (data, callback) => dispatch(postAddTag(data, callback)),
        // postPostUpdateAction: (data, calback) => dispatch(postPostUpdateAction(data, calback)),
        // loadAssetsDataWithDispatch: () => dispatch(loadAssetsDataWithDispatch()),
        // tagsFilter: (refId) => dispatch(tagsFilter(refId)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(ContentModalAside);

