import Select from "react-select";

const SearchDropdown = ({ selected, onSelect, content }) => {

    return <Select styles={{
        control: (baseStyles, state) => ({
            ...baseStyles,
            width: window.location.pathname.includes("moderation") ? "100px" : "120px",
        }),
    }} className="text-align-left" options={[
        { value: "name", label: content?.table_text?.product ?? "Product" },
        { value: "productId", label: content?.table_text?.product_id ?? "Product ID" },
        { value: "sku", label: content?.table_text?.sku ?? "SKU" },
        { value: "productType", label: content?.table_text?.category ?? "Category" },
        { value: "url", label: content?.table_text?.product_url ?? "URL" },
        { value: "tags", label: content?.table_text?.tags ?? "Tags" },
    ]} value={selected} onChange={onSelect} />
}

export default SearchDropdown